.App {
  text-align: center;
  width: 100%;
  height: 100%;
}
.App h3{
  background-color: teal;
  text-shadow: rgba(0,0,0,0.3);
  letter-spacing: 2px;
  font-weight: 900;
  color: aliceblue;
}
.mydiv{
  box-sizing: border-box;
   box-shadow:1px 1px 5px grey;
    display: flex;
    justify-content: space-around;
   padding: 20px;

}
.mydiv input{
width:60%;
}
.gallery{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;

}
.item{
  width: 30%;
  margin:5px;
}
